@import "../var";

.header {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;

    &__top {
        &-inner {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 22px 0;
        }

        &-menu {
            padding: 0;
            display: flex;
            flex-direction: row;

            li {
                list-style: none;
            }
            li + li {
                padding-left: 24px;
            }
        }

        &-phone {
            span {
                color: #979797;
            }
            a {
                padding-left: 6px;
                text-decoration: none;
                color: $primary-text;
            }
        }
    }

    &__bottom {
        border: 1px solid #eeeeee;

        &-inner {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .header__bottom-menu {
                display: none;
            }
        }

        img {
            margin: 9px 0;
            width: 100%;
            height: 100%;
        }

        &-search {
            padding-left: 25px;
            align-items: center;
            input {
                width: 670px;
                min-width: 65px;
                height: 44px;
                padding-left: 20px;
                background: #f8f8f8;
                border: 1px solid #e0e0e0;

                &:focus {
                    outline: none;
                }
            }

            &-icon {
                background: url("../img/search-icon.png") no-repeat center;
                width: 18px;
                height: 18px;
                display: inline-block;
                position: relative;
                left: -30px;
                top: 3px;
                margin-right: -30px;
            }
        }

        &-icon {
            display: flex;
            align-items: center;
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: $primary-text;
            margin-left: 48px;
        }

        &-favoritesHave {
            &::before {
                content: url("../img/heart.png");
                width: 18px;
                height: 18px;
                display: inline-block;
                position: relative;
                left: -20px;
                top: -2px;
            }
        }
        &-favoritesNo {
            &::before {
                content: url("../img/fav3.png");
                width: 18px;
                height: 18px;
                display: inline-block;
                position: relative;
                left: -20px;
                top: -2px;
            }
        }

        &-basket {
            padding-left: 20px;
            border-left: 1px solid rgba(133, 133, 133, 0.15);
            display: inline-flex;
            align-items: center;
            justify-content: flex-start;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: $primary-text;
            padding-left: 35px;

            // span {
            //   padding-left: 10px;
            //   background: url("../img/basket-icon.png") no-repeat center;
            //   position: relative;
            //   left: -30px;
            //   top: 3px;
            // }
        }
        &-basketHave {
            &::before {
                content: url("../img/shopping-bag.png");
                width: 18px;
                height: 18px;
                display: inline-block;
                position: relative;
                left: -20px;
                top: -2px;
            }
        }

        &-basketNo {
            &::before {
                content: url("../img/basket-icon.png");
                width: 18px;
                height: 18px;
                display: inline-block;
                position: relative;
                left: -20px;
                top: -2px;
            }
        }
    }
    a {
        text-decoration: none;
        color: $primary-text;
    }

    &__connection {
        display: flex;
        flex-direction: column;
        height: 90px;
        justify-content: space-between;
        align-items: flex-end;
        position: fixed;
        right: 20px;
        bottom: 30px;
        z-index: 999;

        &-goUp {
            background: url("../img/GoUp.png") no-repeat center;
            width: 28px;
            height: 25px;
            display: flex;
        }
        &-controlButtonClose {
            background: url("../img/Chat.png") no-repeat center;
            width: 28px;
            height: 28px;
            display: flex;
            margin-bottom: 5px;
        }
        &-controlButton {
            background: url("../img/closeButton.png") no-repeat center;
            width: 28px;
            height: 25px;
            display: flex;
            // margin-bottom: 10px;
        }

        &-chat {
            display: flex;
            align-items: center;

            div {
                display: flex;
                justify-content: space-between;
                width: 150px;
                margin-right: 20px;
            }
        }

        &-whatsapp {
            background: url("../img/whatsapp.png") no-repeat center;
            width: 45px;
            height: 45px;
            display: flex;
        }
        &-telegram {
            background: url("../img/telegram.png") no-repeat center;
            width: 45px;
            height: 45px;
            display: flex;
        }

        &-item {
            // position: absolute;
            display: flex;

            &:nth-child(3) {
                background: url("../img/telephone.png") no-repeat center;
                width: 45px;
                height: 45px;
                display: flex;
            }
        }
    }
}

@media screen and (max-width: 700px) {
    .header {
        display: flex;
        flex-direction: column-reverse;
        &__top {
            &-inner {
                .header__top-menu {
                    display: flex;
                    flex-direction: column;
                    // position: absolute;
                    // top: 40px;
                }
            }

            li + li {
                padding-top: 20px;
                padding: 0;
            }

            &-phone {
                display: none;
            }
        }

        &__bottom {
            &-inner {
                display: flex;
                align-items: center;
                justify-content: space-between;

                .header__bottom-menu {
                    display: block;
                    box-sizing: content-box;
                    padding: 8px 9px;
                    border: 1px solid rgba(133, 133, 133, 0.15);
                }
            }

            &-logo {
                margin: 10px 0px;
                width: 99px;
                height: 43px;
            }

            &-search {
                input {
                    display: none;
                }

                &-icon {
                    font-family: "Material Icons";
                    position: relative;
                    left: 0;
                    top: 5px;
                }
            }

            &-favorites {
                display: none;
            }
        }

        &__mobile-menu {
            list-style: none;
            padding: 0;
        }
        &__mobile-menu {
        }
    }

    &__window {
        display: none;
    }
}
